import Layout from '../components/Layout';
import Button from 'react-bootstrap/Button';
const main = {
	backgroundImage:
		'linear-gradient(rgba(0, 0, 0, 0.35), rgba(0, 0, 0, 0.35)),url(https://sun9-50.userapi.com/c858036/v858036219/1b062a/Ime8RDzN648.jpg)',
	position: 'fixed',
	zIndex: '-2',
	top: '0',
	right: '0',
	left: '0',
	backgroundRepeat: 'no-repeat',
	backgroundSize: 'cover',
	height: '100vh',
	display: 'flex',
	flexDirection: 'column',
	justifyContent: 'space-evenly',
	color: 'white',
};
const main__heading = {
	margin: '0 1rem',
	fontWeight: 'bold',
	textAlign: 'center',
	color: 'white',
	fontSize: '1rem',
};
const main__text = {
	...main__heading,
	fontSize: '1.8rem',
	lineHeight: '1.6rem',
	padding: '0 25px',
};
const content = {
	backgroundColor: 'white',
	display: 'flex',
	flexDirection: 'column',
};
const column = {
	display: 'flex',
	flexDirection: 'column',
};
const main__content = {
	height: 'calc(100vh - 70px)',
	display: 'flex',
	flexDirection: 'column',
	justifyContent: 'space-evenly',
	backgroundColor:'transparent'
}
const buy_button = {
	margin: '1rem auto',
};
const about_button = {
	margin: '0 auto',
};
const content__heading = {
	margin:'3vw',
}
const Study1 = () => (
	<Layout>
		<div style={main}>
	
		</div>
		<div style={main__content}>
		<div style={main__text}>
				Создай набор из 3 срезов камней с помощью пошаговой инструкции и наглядного видео-примера!
			</div>
			<div style={column}>
				<h1 style={main__heading}>ГАЙД ПО СОЗДАНИЮ СРЕЗОВ КАМНЕЙ (+видео)</h1>
				<Button style={buy_button}> ХОЧУ НАУЧИТЬСЯ </Button>
				<Button variant="light" style={about_button}>
					подробнее
				</Button>
			</div>
		</div>
		<div style={content}><h2 style={content__heading}>ГАЙД ПО СОЗДАНИЮ СРЕЗОВ КАМНЕЙ (+видео)</h2>
		<Button />
		<p>lorem</p>
		<picture>
		<img src="https://sun9-4.userapi.com/blFxvXJA7QRAkyH_blLRjJdUuLnFcv-0qOC9yg/0tD6e_g2DG8.jpg"/>
		</picture>
		<ul>
			<li></li>
		</ul>
		<div>Q/A</div></div>
	</Layout>
);

export default Study1;
